import './styles/index.scss';
import initCollectionFilters from './product-grid-filter'
import initCollectionPagination from './product-grid-scroll'
import { onDocumentReady } from '../utils/dom';
import { wishlistVariantIdUpdate } from './product-item';

Array.prototype.equals = function (array) {
  if (!array) return false;

  if (this.length !== array.length) {
    return false;
  }

  for (let i = 0, l = this.length; i < l; i++) {
    if (this[i] instanceof Array && array[i] instanceof Array) {
      if (!this[i].equals(array[i])) {
        return false;
      }
    } else if (this[i] !== array[i]) {
      return false;
    }
  }
  return true;
};

Object.defineProperty(Array.prototype, 'equals', { enumerable: false });

onDocumentReady(() => {
  wishlistVariantIdUpdate(),
  initCollectionPagination(),
  initCollectionFilters()
})